import Link from 'next/link';
import "./Toolbar.scss"
import React, {Component} from 'react';


class Toolbar extends Component {

    state = {showToolbarBackground: false};

    componentDidMount = () => {

        this.setState({showToolbarBackground: window.scrollY > 100});
        let ids = ["HeaderComponent", "About", "TeamComponent", "StepsComponent", "ContactUs"];

        window.addEventListener('scroll', (e) => {
            if (window.scrollY > 100) {
                this.setState({showToolbarBackground: true})
            } else {
                this.setState({showToolbarBackground: false})
            }

            for(let i in ids){
                if(ids.hasOwnProperty(i) && this.setActiveSection(ids[i])){
                    this.setState({active: ids[i]});
                    break;
                }
            }


        });

    };

    componentWillUnmount() {
        window.removeEventListener('scroll', false);
    }

    setActiveSection = (elementId) => {
        let el = document.getElementById(elementId);

        return el.offsetTop - window.scrollY < 150;
    };

    render() {

        const {showToolbarBackground, active} = this.state;
        const {scrollTo} = this.props;

        return (<div id="ToolbarCmpnt" className={showToolbarBackground ? "showBackground" : ""}>
            <h1 id="name">Linguartech</h1>

            <span className="flex-1"/>

            <div className={"navOption " + (active === "HeaderComponent" ? "navOptionActive": "")} onClick={() => {
                scrollTo(0)
            }}>
                <h3>Home</h3>
            </div>
            <div className={"navOption " + (active === "About" ? "navOptionActive": "")}  onClick={() => {
                scrollTo("About")
            }}>
                <h3>About</h3>
            </div>
            <div className={"navOption " + (active === "TeamComponent" ? "navOptionActive": "")} onClick={() => {
                scrollTo("TeamComponent")
            }}>
                <h3>Our Team</h3>
            </div>
            <div className={"navOption " + (active === "StepsComponent" ? "navOptionActive": "")} onClick={() => {
                scrollTo("StepsComponent")
            }}>
                <h3>How It Works</h3>
            </div>
            <div className={"navOption " + (active === "ContactUs" ? "navOptionActive": "")} onClick={() => {
                scrollTo("ContactUs")
            }}>
                <h3>Contact Us</h3>
            </div>

        </div>)

    };
}


export default Toolbar;